<template>
    <div style="display:inline-block; height:28px; min-height:28px; line-height:27px;" forceFix="line-height:27px;" :changeParam="changeParam">

        <v-menu
            offset-y
            :disabled="!editable"
            v-model="show"
        >

            <template v-slot:activator="{ on }">
                <div
                    TimedayCore="day"
                    style="display:inline-block; width:90px;"
                    v-on="on"
                >
                    {{getShowTime}}
                </div>
            </template>

            <v-date-picker
                no-title
                locale="zh-tw"
                :color="pickColor"
                :day-format="getDay"
                :value="value_day"
                @input="ch_day"
            ></v-date-picker>

        </v-menu>

    </div>
</template>

<script>
//divmin-height, , div0, 
import split from 'lodash/split'
import get from 'lodash/get'
import isday from 'wsemi/src/isday.mjs'
import cint from 'wsemi/src/cint.mjs'

/**
 * @vue-prop {String} [value=''] ''
 * @vue-prop {String} [pickColor='deep-orange darken-1'] 'deep-orange darken-1'
 * @vue-prop {Boolean} [editable=true] true
 */
export default {
    props: {
        value: {
            type: String,
            default: '',
        },
        pickColor: {
            type: String,
            default: 'deep-orange darken-1',
        },
        editable: {
            type: Boolean,
            default: true,
        },
    },
    data: function() {
        return {
            show: false,
            value_day: '',
        }
    },
    mounted: function() {
    },
    watch: {

        show: function(value) {
            //console.log('watch show')

            let vo = this

            //emit
            vo.$emit('update:focused', value)

        },

    },
    computed: {

        changeParam: function () {
            //console.log('computed changeParam')

            let vo = this

            //value for trigger
            let value = vo.value

            //check
            if (isday(value)) {
                vo.value_day = value
            }
            else {
                vo.value_day = ''
            }

            return ''
        },

        getShowTime: function () {
            //console.log('computed getShowTime')

            let vo = this
            return vo.value_day
        },

    },
    methods: {

        getDay: function (d) {
            //console.log('methods getDay', d)

            let s = split(d, '-')
            s = get(s, '[2]')
            s = cint(s)
            return s
        },

        ch_day: function (d) {
            //console.log('methods ch_day', d)

            let vo = this

            //value_day
            let value_day = d

            //check
            let value = ''
            if (isday(value_day)) {
                value = value_day
            }

            //hide
            vo.show = false

            //setTimeout
            setTimeout(function() {

                //emit
                vo.$emit('input', value)

            }, 1)

        },

    },
}
</script>

<style scoped>
</style>
